@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&family=Oswald:wght@200;300;400;500;600;700&display=swap);
@import url(https://pro.fontawesome.com/releases/v5.10.0/css/all.css);
/*https://technext.github.io/confer/index.html*/

body {
    color: #797979;
    background: #ffffff;
    font-family: 'Lato', sans-serif;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
}
 :focus {
    outline:none !important
}

h1, h2, h3, h4, h5, h6 {
    line-height: 1.2;
    color: #092a49;
    font-family: 'Oswald', sans-serif;
}

h1 {
    font-size: 2.5rem;
}

 h2 {
    font-size: 50px;
    font-weight: 200;
    position: relative;
    margin-left: 40px;
}
h2::after {
    content: "";
    position: absolute;
    width: 200px;
    height: 0;
    top: 23px;
    left: -40px;
    border-bottom: 30px solid;
    border-image: repeating-linear-gradient( 60deg, rgba(0,0,0,.05), rgba(0,0,0,.05) 1.5%, rgba(0,0,0,.1) 1.5%, rgba(0,0,0,.1) 3%) 30;
}

 h3 {
    font-size: 22px;
    font-weight: 300;
    letter-spacing: 1px;
}
h4 {
    font-size:16px;
    color: #797979;
    font-family: 'Lato', sans-serif;
}


.fact {
    padding:60px 0;
}

.fact .fact-item {
    position: relative;
    width: 100%;
    margin-bottom: 30px;
    padding: 30px 0 25px 0;
    text-align: center;
    background: #ffffff;
    border: 1px solid transparent;
    box-shadow: 0 0 30px rgba(0, 0, 0, .1);
    transition: .3s;
}

.fact .fact-item img {
    max-height: 50px;
    margin-bottom: 15px;
}

.fact .fact-item:hover {
    border-color: rgba(0, 0, 0, .1);
    box-shadow: none;
}

.about {
    position: relative;
    padding: 45px 0;
}

.about .about-img {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    transition: .5s;
}

.about .about-img-1 {
    position: relative;
    margin: 0 150px 150px 0;
}

.about .about-img-1 img, 
.about .about-img-2 img {
    position: relative;
    width: 100%;
}

.about .about-img-2 {
    position: absolute;
    top: 150px;
    left: 150px;
    z-index: 1;
}

.about .about-img::after {
    position: absolute;
    content: "";
    top: 60px;
    right: 60px;
    bottom: 60px;
    left: 60px;
    background: transparent;
    border: 30px solid;
    border-image: repeating-linear-gradient( 45deg, rgba(0,0,0,.05), rgba(0,0,0,.05) .5%, rgba(0,0,0,.1) .5%, rgba(0,0,0,.1) 1%) 30;
    z-index: 1;
}

.about .about-text a.btn {
    position: relative;
    margin-top: 15px;
    padding: 12px 25px;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 1px;
    color: #092a49;
    border: 2px solid #092a49;
    border-radius: 0;
    background: none;
    transition: .3s;
}

.about .about-text a.btn:hover {
    color: #0796fe;
    background: #092a49;
}


.service .service-item {
    position: relative;
    width: 100%;
    margin-bottom: 30px;
    padding: 30px;
    text-align: center;
    border: 1px solid rgba(0, 0, 0, .1);
    transition: .3s;
}

.service .service-item img {
    max-height: 50px;
    margin-bottom: 20px;
}

.service .service-item a {
    position: relative;
    font-size: 16px;
    color: #0796fe;
    transition: .3s;
    text-decoration: none;
}

.service .service-item a::after {
    content: "";
    position: absolute;
    width: 80%;
    height: 2px;
    bottom: -3px;
    left: 10%;
    background: #0796fe;
}

.service .service-item:hover {
    border-color: transparent;
    box-shadow: 0 0 30px rgba(0, 0, 0, .1);
}
.service {
    position: relative;
    width: 100%;
    padding: 45px 0 15px 0;
}
.section-header {
    position: relative;
    text-align: left;
    margin-bottom: 45px;
}

.feature {
    position: relative;
    padding: 45px 0;
}

.feature .feature-img {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
}

.feature .feature-img img {
    position: relative;
    max-width: 100%;
    max-height: 100%;
    margin: 0 30px 30px 30px;
    z-index: 2;
}

.feature .feature-img::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 70%;
    top: 30%;
    left: 0;
    border: 30px solid;
    border-image: repeating-linear-gradient( 45deg, rgba(0,0,0,.05), rgba(0,0,0,.05) .5%, rgba(0,0,0,.1) .5%, rgba(0,0,0,.1) 1%) 30;
    z-index: 9;
}
.counters-text{
    display: inline-block;
    
}
.feature .counters i {
    padding: 5px 15px 0 0;
    font-size: 28px;
    color: #0796fe;
    vertical-align: top;
}

.feature .counters h3 {
    position: relative;
    display: inline-block;
    color: #0796fe;
    font-size: 30px;
    font-weight: 300;
}

.feature .counters h3::after {
    position: absolute;
    content: "+";
    top: -15px;
    right: -15px;
}

.feature .counters p {
    color: #092a49;
    font-size: 20px;
    margin: 0;
}

.about,
.service,
.feature,
.team,
.review,
.contact{
    padding:60px 0;
}


.contact .contact-info {
    position: relative;
    display: flex;
    padding:20px;
    border: 1px solid rgba(0, 0, 0, .1);
    margin-bottom: 30px;
    transition: .3s;
}
.contact .contact-info:hover {
    border-color: transparent;
    box-shadow: 0 0 30px rgba(0, 0, 0, .1);
}
.contact .contact-icon {
    position: relative;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid rgba(0, 0, 0, .1);
}

.contact .contact-icon i {
    font-size: 18px;
    color: #0796fe;
}
.contact .contact-text {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-left: 15px;
}

.contact .contact-form {
    position: relative;
    padding:30px;
    border: 1px solid rgba(0, 0, 0, .1);
    margin-bottom: 30px;
    transition: .3s;
}
.contact .contact-form:hover {
    border-color: transparent;
    box-shadow: 0 0 30px rgba(0, 0, 0, .1);
}
.contact .contact-text h3 {
    font-size: 16px;
    font-weight: 400;
    color: #092a49;
    letter-spacing: 1px;
}

.contact .contact-form textarea {
    min-height: 100px;
    padding: 8px 15px;
    background: none;
    border-radius: 0;
    border: 1px solid rgba(0, 0, 0, .1);
}
.contact .contact-form .btn {
    padding: 15px 30px;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 1px;
    color: #092a49;
    background: none;
    border: 2px solid #092a49;
    border-radius: 0;
    transition: .3s;
}

.contact .contact-form .btn:hover {
    color: #0796fe;
    background: #092a49;
}

.team .team-item {
    position: relative;
    margin-bottom: 75px;
}

.team .team-img {
    position: relative;
}

.team .team-text {
    position: absolute;
    width: calc(100% - 30px);
    height: 96px;
    bottom: -46px;
    left: 15px;
    padding: 25px 15px;
    text-align: center;
    background: rgba(256, 256, 256, .9);
    box-shadow: 0 0 30px rgba(0, 0, 0, .1);
    transition: .3s;
    overflow: hidden;
}

.team .team-text h5 {
    color: #0796fe;
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 1px;
    margin-bottom: 10px;
}

.team .team-text p {
    margin-bottom: 20px;
    color: #092a49;
}

.team .team-social {
    position: relative;
    font-size: 0;
}

.team .team-social a {
    display: inline-block;
    width: 35px;
    height: 35px;
    margin: 0 3px;
    padding: 5px 0;
    text-align: center;
    font-size: 14px;
    color: #092a49;
    border: 2px solid #092a49;
    transition: .3s;
}
.team .team-social a:hover {
    color: #0796fe;
    border-color: #0796fe;
}
.team .team-item:hover .team-text {
    height: 160px;
}

.back-to-top {
    position: fixed;
    background: #0796fe;
    width: 44px;
    height: 44px;
    text-align: center;
    line-height: 1;
    font-size: 22px;
    right: 15px;
    bottom: 15px;
    transition: 0.5s;
    z-index: 9;
    cursor: pointer;
}

.back-to-top i {
    color: #092a49;
    padding-top: 10px;
}
.top-bar {
    position: relative;
    padding: 0 60px;
    background-image: linear-gradient(to right, #0796fe, #092a49);
}

.top-bar .top-bar-left {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.top-bar .text {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    height: 35px;
    padding: 0 10px;
    text-align: center;
    border-left: 1px solid rgba(255, 255, 255, .15);
    font-family: 'Oswald', sans-serif;
}

.top-bar .text i {
    font-size: 14px;
    color: #ffffff;
    margin-right: 5px;
}

.top-bar .text span {
    color: #eeeeee;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 1px;
    margin: 0;
}

.top-bar .text strong {
    color: #eeeeee;
    font-size: 14px;
    font-weight: 400;
    margin: 0 0 0 5px;
    font-family: 'Lato', sans-serif;
}

.top-bar .header-social {
    display: flex;
    justify-content: flex-end;
    list-style: none;
    margin: 0;
    padding: 0;
}

.top-bar .header-social li a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 35px;
    height:35px;
    font-size: 16px;
    color: #ffffff;
    border-right: 1px solid rgba(255, 255, 255, .15);
    text-decoration: none;
}

.top-bar .header-social li a:hover {
    color: #092a49;
    background: #ffffff;
}
 

.header-bottom{
    padding:12px 60px;
    background: #092a49;
    position: absolute;
    left: 0;
    right: 0;
    z-index: 99;
    transition: .5s;
}

.home .header-bottom{
    background: rgba(9, 42, 73, .2);
}

.header-bottom-wrapper{
    min-height: 79px;
}

.home .header-bottom-wrapper{
    min-height:0;
}

.sticky .header-bottom{
    position: fixed;
    padding:2px 60px;
    background: #092a49;
    top: 0;
}


@media (max-width: 991px){
    .home .header-bottom,
    .header-bottom,
    .sticky .header-bottom{
        background: #092a49;
        padding:8px 15px;
        position: absolute;
    }

}
.navbar{
     
}
.navbar-expand-lg {
    justify-content: flex-end;
    padding-right: 0;
    padding-left: 0;
}

.navbar-brand{
    color: #ffffff;
    font-size: 45px;
    line-height:1; 
    text-transform: uppercase;
}

.navbar-expand-lg .navbar-nav .nav-link {
    padding: 10px 10px 8px 10px;
    color: #ffffff;
    font-size: 14px;
    letter-spacing: 1px;
    text-transform: uppercase;
}

.navbar-expand-lg .navbar-nav > .nav-item:hover > .dropdown-toggle,
.navbar-expand-lg .navbar-nav .nav-link:hover {
    color: #0796fe;
}



.MenuTrigger {
    position: absolute;
    top:-48px;
    right: 0;
    display: inline-block;
    padding:5px 15px;
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: .25rem;
    width:56px;
    cursor: pointer;
}

.MenuTrigger span{
    height: 2px;
    background:rgba(255, 255, 255, 0.3);
    display: block;
    margin:6px 0;
}

.navbar-expand-lg .navbar-nav > .nav-item{
    position: relative;
}

.navbar-expand-lg .navbar-nav > .nav-item.dropdown > .nav-link{
    padding-right:15px;
} 

.dropdown-toggle{
    position: absolute;
    right: 0;
    top:8px;
    cursor: pointer;
    color: #fff;
}
.DropClose{
    display: none;
}
.DropOpen{
    display: block;
    position: absolute;
    background: #092a49;
    min-width: 150px;
}

@media (max-width: 991px){
   
    .navbar-expand-lg   {
        background: #092a49;
        justify-content: flex-start;
        padding: 0;
    }

    .MenuClose .navbar-nav{
        display: none;
    }

    .MenuClose.MenuOpen .navbar-nav{
        display:block;
    }

    .DropOpen{
        position: static;
    }

    .MenuClose.MenuOpen{
        width: 100%;
    }

}
footer {
    position: relative;
    margin-top: 45px;
    padding: 90px 20px 20px;
    background: #092a49;
    color: #fff;
}

footer h3 {
    position: relative;
    margin-bottom: 30px;
    padding-bottom: 10px;
    font-size: 22px;
    font-weight: 300;
    letter-spacing: 1px;
    color: #ffffff;
}

footer h3::after {
    position: absolute;
    content: "";
    width: 45px;
    height: 2px;
    bottom: 0;
    left: 0;
    background: #0796fe;
}

footer .footer-contact p i {
    width: 25px;
}

footer .footer-contact, 
footer .footer-link, 
footer .footer-newsletter {
    position: relative;
    margin-bottom: 45px;
    color: #ffffff;
}

footer .footer-link ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

footer .footer-link ul li a {
    display: block;
    margin-bottom: 10px;
    color: #ffffff;
    transition: .3s;
    text-decoration: none;
}

footer .footer-link ul li a::before {
    position: relative;
    content: "\f105";
    font-family: "Font Awesome 5 Pro";
    font-weight: 900;
    margin-right: 10px;
}

footer .footer-link a:hover {
    color: #0796fe;
    letter-spacing: 1px;
}

footer .footer-social {
    position: relative;
    margin-top: 20px;
    padding: 0;
    list-style: none;
    display: flex;
}

footer .footer-social a i {
    margin-right: 15px;
    font-size: 18px;
    color: #fff;
}

footer .footer-newsletter .form {
    position: relative;
    width: 100%;
}

footer .footer-newsletter input {
    height: 60px;
    border: 2px solid #1d2434;
    border-radius: 0;
}

footer .footer-newsletter .btn {
    position: absolute;
    top: 8px;
    right: 8px;
    height: 44px;
    padding: 8px 20px;
    font-size: 14px;
    font-weight: 600;
    color: #092a49;
    background: none;
    border-radius: 0;
    border: 2px solid #092a49;
    transition: .3s;
}
footer .footer-newsletter .btn:hover {
    color: #0796fe;
    background: #092a49;
}

footer .copyright{
    padding:30px 0 0;
}

footer .copyright .row {
    padding-top: 25px;
    padding-bottom: 25px;
    border-top: 1px solid rgba(256, 256, 256, .1);
}

footer .copyright p {
    margin: 0;
    color: #999999;
}

footer .copyright p a {
    color: #ffffff;
}
.slider {
  
}
.slider .container-fluid {
    padding: 0;
}
 

 
.slider-item-inner{
    text-align: center;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 150px 0 100px;
}

.slider .slider-item-inner:after {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(9, 42, 73, .7);
    z-index: 1;
}

.slider .slider-text{
    position: relative;
    z-index: 9;
    padding: 0 20px;
}
.slider .slider-text h2 {
    text-align: center;
    color: #ffffff;
    font-size: 60px;
    font-weight: 200;
    margin-bottom: 20px;
}

.slider .slider-text p {
    color: #ffffff;
    text-align: center;
    font-size: 20px;
    margin-bottom: 25px;
}

.slider .slider-text .btn {
    padding: 15px 30px;
    font-size: 16px;
    letter-spacing: 1px;
    color: #ffffff;
    background: none;
    border: 2px solid #ffffff;
    border-radius: 0;
    transition: .3s;
    margin:10px;
}

.slider .slider-text .btn i {
    margin-right: 5px;
}

.slider .slider-text .btn:hover {
    color: #0796fe;
    background: #ffffff;
}

 .slick-arrow{
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 9;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #0796fe;
    background: #092a49;
    font-size: 22px;
    transition: .3s;
    border: none;
    font-size: 0;
    font-weight: bold;
    font-family: "Font Awesome 5 pro";
}
 .slick-arrow.slick-next:after{
    content: "\f105";
    font-size: 22px;
    
}

 .slick-arrow.slick-prev:after{
    content: "\f104";
    font-size: 22px;
}

 .slick-arrow.slick-prev{
    right: 62px;
}

 .slick-arrow:hover{
color: #ffffff;
    background: #0796fe;
}


@media (max-width: 991px){
    .slider .slider-text h2 {
        font-size:40px;
    }

    .slider .slider-text .btn {
        padding: 10px 20px;
    }

    .slider .slider-text p {
        font-size:18px;
        margin-bottom: 20px;
    }

    .slider-item-inner{
        padding:100px 0 50px;
    }

}
.testimonial{
    padding: 50px 0 80px;
}
.testimonial .testimonial-item img {
    position: relative;
    width: 80px;
    border-radius: 80px;
    margin-bottom: 15px;
}

.testimonial .testimonial-item h2 {
    position: relative;
    color: #092a49;
    font-size: 16px;
    letter-spacing: 1px;
    margin: 0 0 5px;
    padding-bottom: 3px;
}

.testimonial .testimonial-item h2:after {
    position: absolute;
    content: "";
    width: 30px;
    height: 2px;
    top:auto;
    bottom: 0;
    left: 0;
    background: #0796fe;
    transition: .3s;
    border-bottom: none;
}
.testimonial .testimonial-item:hover h2:after {
    width: 60px;
}
.testimonial .testimonial-item h3 {
    color: #999999;
    font-size: 14px;
    font-weight: 300;
    letter-spacing: 2px;
    margin: 0;
}

.testimonial .slick-arrow{
     bottom:-90px;
    margin: 0 auto;
    left: 0;
    right: 0;
    border-radius: 50%;
}


.testimonial .slick-arrow.slick-prev{
    right: 65px;
}

.testimonial .slick-arrow.slick-next{
    left: 65px;
}

.testimonial-item{
    padding: 10px;
}
